import React from "react";
import { StaticImage } from "gatsby-plugin-image";

import About from "../components/About";
import Crew from "../components/Crew";
import FAQs from "../components/FAQs";
import Graffiti from "../components/Graffiti";
import Layout from "../components/Layout";
import MTen from "../components/MTen";
import LMIcF from "../components/LMIcF";
import World from "../components/World";
import YorudanDistrikt from "../components/YorudanDistrikt";
import "../i18n";
import { withTranslation } from "react-i18next";
// import Mint from "../components/Mint";

const IndexPage = ({ t }) => {
  const [showCS, setShowCS] = React.useState(false);

  function handleClick(e) {
    if (showCS) return;
    setShowCS(true);
    let btn = e.currentTarget;
    btn.classList.add("minting");
    setTimeout(() => {
      setShowCS(false);
      btn.classList.remove("minting");
    }, 2000);
  }

  return (
    <Layout>
      <div className="scroll-snap banner" id="index">
        <StaticImage
          src="../images/distrikt-feature-image.png"
          alt="Hero"
          placeholder="blurred"
        />
        <button className="mint" onClick={handleClick}>
          {showCS ? <span>{t("coming_soon")}!</span> : <span>Mint Now</span>}
        </button>
      </div>
      {/* <Mint /> */}
      <About />
      <World />
      <MTen />
      <YorudanDistrikt />
      <Graffiti />
      <LMIcF />
      <Crew />
      <FAQs />
    </Layout>
  );
};

export default withTranslation()(IndexPage);
