import React from "react";
import { StaticImage } from "gatsby-plugin-image";
import { withTranslation } from "react-i18next";

function About({ t, i18n }) {
  return (
    <div className="scroll-snap about-content" id="about">
      <div className="logo">
        {i18n.language === "es" ? (
          <StaticImage
            src="../images/about-logo-es.png"
            alt="Hero"
            placeholder="blurred"
          />
        ) : (
          <StaticImage
            src="../images/about-logo.png"
            alt="Hero"
            placeholder="blurred"
          />
        )}
      </div>
      <p dangerouslySetInnerHTML={{ __html: t("about_description") }}></p>
    </div>
  );
}

export default withTranslation()(About);
